import React, {useState} from "react";
import ButtonBase from "@material-ui/core/ButtonBase";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Form} from 'react-bootstrap'
import EditableLabel from "../../../../../ReusableComponents/EditableLabel/EditableLabel";

const useStyles = makeStyles((theme) => ({
  base: {
    display: "block", 
    padding: "6px 12px",
    paddingBottom: "2px",
    height: "fit-content !important",
    margin: "0 !important",
    pointerEvents: "none",
    "&:focus":{
      "box-shadow": "0 0 2pt 2pt #3687dd !important",
      zIndex:"999999"
    },
  },
  labelBox: {
    padding: "6px 18px",
    display: "flex",
    borderBottom: "3px solid transparent",
    lineHeight: "initial !important",
    alignItems: "center",
    gap: "2px",
    fontWeight: "600"
  },
  elipses: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    display: "block",
    width: "100%",
  },
}));

const TabBox = ({ tab, direction }) => {
  const classes = useStyles({ direction });

  const [editLabel, setEditLabel] = useState(false);
  const bringEditableLabel = (event) => {
      setEditLabel(true);
  };

  const [defaultLabel, setDefaultLabel] = useState(
    tab?.child?.label || tab?.label
        ? tab?.child?.label || tab?.label
        : ''
  );

  const setLabelHandler = (recievedLabel) => {
      try{
          if (tab?.child?.label || tab?.label){
              setDefaultLabel(
                  (prevDefaultLabel) => {
                      if(prevDefaultLabel===recievedLabel){
                          return prevDefaultLabel;
                      } else {
                          tab?.setUpdatedLabel(recievedLabel);
                          return recievedLabel;
                      }
                  }
              );
              setEditLabel(false);
          }
      }catch(err){
          console.error(err);
      }
  }

  return (
    <ButtonBase className={`tabBox ${classes.base}`} tabIndex="0">
      <div
        className={`labelBox ${classes.labelBox}`}
        id={tab?.child?.id}
        elem_type={'sheet'}
        style={tab?.IconLabelAlignment === 'Top Bottom' ? {flexDirection: 'column'} : {}}
        control_id={tab?.control_id}
        frameIndex={tab?.frameIndex}
        elementIndex={tab?.elementIndex}
        sheetIndex={tab?.i}
        sheetName={tab?.child?.label || tab?.label}
      >

        {(tab?.icon?.url || tab?.icon) ? (
          <img
            alt="tabImg"
            src={tab?.icon?.url || `${tab.imageBaseURL}${tab?.icon}`}
            width={tab?.icon?.width || "16px"}
            height={tab?.icon?.height || "16px"}
            style={{margin :"0 2px"}}
          />
        ) : null}

        {tab?.iconElem && tab.iconElem}

        <EditableLabel 
              defaultLabel={
                <Form.Label  
                  onDoubleClick={bringEditableLabel}
                  style={{marginBottom: '0pt',lineHeight: 'initial'}}
                >
                    <span style={tab?.sheetIndex1 === tab?.i && tab?.styleConfig?.apiStyle?.color ? {...tab.style, color: tab.styleConfig.apiStyle.color} : tab?.sheetIndex2 === tab?.i && tab?.styleConfig?.apiStyle?.background ? {...tab.style, background: tab.styleConfig.apiStyle.background} : tab.style}>{tab?.child?.label || tab?.label}</span>
                </Form.Label> 
              }
              editlabel={editLabel}
              default_value={defaultLabel}
              setLabelHandler={(recievedLabel)=>setLabelHandler(recievedLabel)}
        />
      </div>
    </ButtonBase>
  );
};

export default TabBox;