import React, { useEffect, useRef, useState } from "react";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import Popper from "@material-ui/core/Popper";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import Typography from "@material-ui/core/Typography";
import Collapse from "@material-ui/core/Collapse";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";

function MenuButton({
  label,
  submenus = [],
  id,
  onClick,
  component,
  direction = "ltr",
}) {
  const [open, setOpen] = useState(!!submenus.length ? false : null);

  return (
    <>
      <MenuItem
        id={id}
        style={{ padding: "0.5rem", direction }}
        onClick={(evt) => {
          if (!!submenus.length) setOpen(!open);
          else onClick(evt, { label, id, component });
        }}
      >
        <Typography
          noWrap
          style={{
            fontSize: "0.75rem",
            fontWeight: "600",
            fontColor: "#202020",
            fontFamily:"Open Sans",
            textTransform:"capitalize"
          }}
        >
          {label}
        </Typography>
        {!!submenus.length && (open ? <ExpandLess /> : <ExpandMore />)}
      </MenuItem>
      {!!submenus.length && (
        <Collapse
          component="li"
          in={open}
          timeout="auto"
          unmountOnExit
          style={{
            paddingLeft: direction === "ltr" ? "0.5rem" : "",
            paddingRight: direction === "rtl" ? "0.5rem" : "",
          }}
        >
          {submenus.map((submenu) => (
            <MenuButton
              key={submenu.id}
              {...submenu}
              onClick={onClick}
              direction={direction}
            />
          ))}
        </Collapse>
      )}
    </>
  );
}

export default function MenuListComposition({
  anchorRef,
  open,
  onClose,
  onSelect,
  menuItems,
  direction,
}) {
  const handleClose = (event) => {
    if (anchorRef && anchorRef.contains(event.target)) {
      return;
    }

    onClose(false);
  };

  function handleListKeyDown(event) {
    if (event.key === "Tab") {
      event.preventDefault();
      onClose(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = useRef(open);
  useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.focus();
    }

    prevOpen.current = open;
  }, [open]);

  return (
    <Popper
      open={open}
      anchorEl={anchorRef}
      role={undefined}
      transition
      disablePortal
      style={{ zIndex: 99999 }}
    >
      {({ TransitionProps, placement }) => (
        <Grow
          {...TransitionProps}
          style={{
            transformOrigin:
              placement === "bottom" ? "center top" : "center bottom",
          }}
        >
          <Paper>
            <ClickAwayListener onClickAway={handleClose}>
              <MenuList
                autoFocusItem={open}
                id="menu-list-grow"
                onKeyDown={handleListKeyDown}
              >
                {!!menuItems?.length &&
                  menuItems.map((item, index) => (
                    <MenuButton
                      key={item.id}
                      direction={direction}
                      {...item}
                      onClick={(evt, subItem) => {
                        handleClose(evt);
                        onSelect(subItem || item, index);
                      }}
                    />
                  ))}
              </MenuList>
            </ClickAwayListener>
          </Paper>
        </Grow>
      )}
    </Popper>
  );
}
