import React, { forwardRef, useEffect, useMemo, useReducer, useRef, useState} from "react";
import Button from "@material-ui/core/Button";

import TabBox from "./components/TabBox";
import MenuBox from "./components/Menu";
import ELEMENTS from "../../ELEMENTS";

import reducer from "./reducer";
import BackIcon from "../../../../Icons/PickListIcons/LeftArrow.svg";
import NextIcon from "../../../../Icons/PickListIcons/RightArrow.svg";
import useStyles from "./style";
import _ from "lodash";

const Div = forwardRef((props, ref) => (
  <div ref={ref} {...props} className={props.className} />
));

const parseList = (list = []) =>
  list
    .filter((item) => !item.hidden)
    .map((item) => {
      const { component, submenus, ...rest } = item;
      return { submenus: parseList(submenus), ...rest};
    });

const FlexMenu = ({
  minWidth,
  className,
  selectedIndex,
  setactiveIndex,
  imageBaseURL,
  selectedClass,
  list,
  apiChangeIndex,
  setUpdatedLabel,
  template,
  direction = "ltr",
  onSelect,
  styleConfig = {},
  saveDisable,
  sheetIndex1,
  sheetIndex2,
  handleSelected,
  setSaveDisable,
  showBorder,
  actions,
  frameIndex,
  elementIndex,
  enable,
  readOnly,
  readOnlyValue,
  control_id,
  data,
  id,
  name,
  elem_type,
  isPortal,
  handleReorderDrop,
  containerId,
  previewmode
}) => {
  
  const translationStyles1=window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA";
  const outerNav = useRef(null);
  const nav = useRef(null);
  const actionNav = useRef(null);
  const items = useRef(new Map()).current;
  const classes = useStyles({ ...styleConfig, direction, items, selectedIndex });

  const [menuList, setMenuList] = useState(parseList(list));
  const [showMenu, setShowMenu] = useState(false);

  const initialState = {
    children: menuList,
    dropdownItems: [],
    lastItemWidth: [],
    inActiveTabWidth: '',
  };
  const [state, dispatch] = useReducer(reducer, initialState);

  useEffect(() => {
    const parsedList = parseList(list);
    if (JSON.stringify(menuList) !== JSON.stringify(parsedList)) {
      dispatch({
        type: "reset",
        payload: parsedList,
      });
      setMenuList(parsedList);
    }
  }, [menuList, list]);

  const updatedList = useMemo(() => list, [list]);;

  useEffect(() => {
    if(items && items.get(selectedIndex) && document.getElementById(`parentTabBox${id}`)) 
      if(document.getElementById(`parentTabBox${id}`)){
        document.getElementById(`parentTabBox${id}`).style.minWidth = `${items.get(selectedIndex).clientWidth}px`;
      }
    if(selectedIndex === items.size - 1) 
      setSaveDisable(true);
    else setSaveDisable(false);
    },[nav.current?.offsetWidth,selectedIndex])

  useEffect(() => {
    const scrollWidth =  document.getElementById(`parentTabBox${id}`)?.scrollWidth;
    const scrollLeft =  document.getElementById(`parentTabBox${id}`)?.scrollLeft;
    const offsetWidth =  document.getElementById(`parentTabBox${id}`)?.offsetWidth;

    if(Math.abs(scrollLeft) + offsetWidth >= scrollWidth - 1) {
        setMaxRight(true);
    } else setMaxRight(false);
    if(scrollLeft === 0 || scrollLeft === 1) {
        setMaxLeft(true);
    } else setMaxLeft(false);
  },[updatedList])

  const visible = data?.style?.Visible || 'true';

  let inlineTabStyle = {minHeight:"100%", overflowX:"auto",overflowY:"hidden", direction: translationStyles1 ? "rtl" : "ltr"};

  if (visible === "false" || visible===false){
    inlineTabStyle['display'] = 'none';
  }

  const [maxLeft,setMaxLeft] = useState(true);
  const [maxRight,setMaxRight] = useState(false);



  const handleScroll = (e,direction) => {
    if(direction === "left")
      document.getElementById(`parentTabBox${id}`).scrollLeft -= (document.getElementById(`parentTabBox${id}`)?.offsetWidth)/2;
    else  document.getElementById(`parentTabBox${id}`).scrollLeft += (document.getElementById(`parentTabBox${id}`)?.offsetWidth)/2;
    const scrollWidth =  document.getElementById(`parentTabBox${id}`)?.scrollWidth;
    const scrollLeft =  document.getElementById(`parentTabBox${id}`)?.scrollLeft;
    const offsetWidth =  document.getElementById(`parentTabBox${id}`)?.offsetWidth;
    if(Math.abs(scrollLeft) + offsetWidth >= scrollWidth - 1) {
      setMaxRight(true);
    } else setMaxRight(false);
    if(scrollLeft === 0 || scrollLeft === 1) {
      setMaxLeft(true);
    } else setMaxLeft(false);
  } 

  useEffect(() => {
      if(apiChangeIndex || apiChangeIndex === 0)
        callCustomHooksAfterSheetComponentLoaded();
  },[apiChangeIndex]);

  const callCustomHooksAfterSheetComponentLoaded=()=>{
    if(previewmode){
      // Custom Hook Function
      if(window.onClickTab)
      { // tabId,sheetindex,eventCall(2 =>when sheet component loaded)
        setTimeout(()=>{
          window.onClickTab(id,selectedIndex,1);
        },0)
      }
      if(window.onClickTabByName)
      { // tabId,sheetLabel,eventCall
        setTimeout(()=>{
          window.onClickTabByName(id,list[selectedIndex]?.Label,1);
        },0)
      }
    }
  }
    return (
      <div id={id} name={name} elem_type={elem_type} style={inlineTabStyle} enable={enable} readonly={readOnly} readOnlyValue={readOnlyValue} onClick={(e) => {
        e.stopPropagation()
      }}>
        <Div
          control_id={control_id}
          style={{...styleConfig.container,minWidth: minWidth, direction, flex: "1"}}
          ref={outerNav}
          className={`${classes.PriorityNav_Root} ${className}`}
        >
            {!maxLeft && <img alt={ translationStyles1 ? "Next Icon": "Back Icon"}
              src={ translationStyles1 ? NextIcon: BackIcon}
              className="BackIcon"
              onClick={(e)=>handleScroll(e, translationStyles1 ? "right" : 'left')}
            />}
          <Div ref={nav} id={`parentTabBox${id}`} className={classes.PriorityNav_Main} 
            onDrop={(ev) => handleReorderDrop(ev, 'frame')}
            frameIndex={frameIndex}
          >
            {updatedList.map((child, i) => (
                <Div
                  ref={(s) => {
                    if (s) {
                      items.set(i, s);
                    }
                  }}
                  className={`${classes.PriorityNav_Item} ${
                    selectedIndex === i
                      ? selectedClass || classes.selectedMenu
                      : styleConfig.label
                  }`}
                  style={sheetIndex2 && sheetIndex2 === i && styleConfig?.apiStyle?.background ? {background: styleConfig.apiStyle.background, display:child.hidden?'none':"block"} : {display:child.hidden?'none':"block"}}
                  key={i}
                  id={`menuItem_${child.id || i}`}
                  control_id={id}
                  elem_type="sheet"
                  onKeyDown={(event)=>{
                    if(event.code==="Space"){
                      setactiveIndex(i)
                      if(previewmode){
                        // Custom Hook Function
                        if(window.onClickTab)
                        { // tabId,sheetindex,eventCall(1 =>when clicked on sheet)
                            window.onClickTab(id,i,2);
                        }
                        if(window.onClickTabByName)
                        { // tabId,sheetLabel,eventCall
                            window.onClickTabByName(id, list[i]?.Label,2);
                        }
                      }
                      if(!previewmode) {
                        handleSelected(event);
                      }
                    }
                  }}
                  onClick={(e) => {
                    setactiveIndex(i)
                    if(previewmode){
                      // Custom Hook Function
                      if(window.onClickTab)
                      { // tabId,sheetindex,eventCall(1 =>when clicked on sheet)
                          window.onClickTab(id,i,2);
                      }
                      if(window.onClickTabByName)
                      { // tabId,sheetLabel,eventCall
                          window.onClickTabByName(id, list[i]?.Label,2);
                      }
                    }
                    if(!previewmode) {
                      handleSelected(e);
                    }
                  }}
                >
                  {template({
                    ...child,
                    label: child?.label,
                    id: "sheetHeader_" + child.id,
                    control_id: child.id,
                    elem_type: elem_type,
                    child: child,
                    IconLabelAlignment: styleConfig.IconLabelAlignment,
                    frameIndex,
                    elementIndex,
                    setUpdatedLabel: setUpdatedLabel,
                    styleConfig,
                    sheetIndex1,
                    sheetIndex2,
                    imageBaseURL,
                    i,
                    style: selectedIndex === i ? {...styleConfig.label.active,fontSize:`${styleConfig.label.active.size}`,fontWeight:`${styleConfig.label.active.weight}`} : {...styleConfig.label,fontSize:`${styleConfig.label.size}`,fontWeight:`${styleConfig.label.weight}`},
                    icon: child?.icon,
                    containerId:containerId
                  })}
                </Div>
            ))}
          </Div>  
            {!maxRight && <img alt={ translationStyles1 ? "Next Icon": "Back Icon"}
              src={ translationStyles1 ? BackIcon: NextIcon}
              className="NextIcon"
              onClick={(e) => handleScroll(e, translationStyles1 ? "left" :'right')}
            />}
          {/* ACTION BAR */}
          {!!actions.length && (
            !isPortal ?
            <div className={classes.actionBar}>
              <span ref={actionNav}>
                {actions.map(({ label, id, fill, ...rest }) => (
                  <Button
                  disableFocusRipple
              disableRipple
              tabIndex={0}
                    className={`${id==="save" ? classes.actionItemSaveNext : classes.actionItemSave} formBuilder-MuiButton-outlined`}
                    variant={fill ? "contained" : "outlined"}
                    color="primary"
                    disabled={id === "save" ? false : saveDisable}
                    style={id === "save" ? {...styleConfig?.actionItemSave} : {...styleConfig?.actionItemSaveNext}}
                    {...rest}
                  >
                    {label}
                  </Button>
                ))}
              </span>
            </div> : <></>
          )}
        </Div>
        <div
          key={list[selectedIndex]?.id}
          control_id={list[selectedIndex]?.id}
          elem_type='tabChild'
          id={list[selectedIndex]?.id}
          activeJson={JSON.stringify(list[selectedIndex]?.component)}
          className={classes.contentBox}
          onClick={(e) => handleSelected(e)}
          style={{...styleConfig?.contentBox, ...(showBorder === "false" || showBorder === false ? {borderWidth: '0px'}: { borderColor: styleConfig?.contentBox?.border?.color, borderWidth: styleConfig?.contentBox?.border?.size})}}
        >
          <div
          id={list[selectedIndex]?.id}
          control_id={list[selectedIndex]?.id}
          elem_type="tabChild"
          className={list[selectedIndex]?.conditionalClasses}
          elementIndex={list[selectedIndex]?.elementIndex}
          frameIndex={list[selectedIndex]?.frameIndex}
          sheetIndex={list[selectedIndex]?.sheetId}
          sheetName={list[selectedIndex]?.Label}
          style={{minHeight:"inherit",paddingBottom:"20pt"}}
        >
            <ELEMENTS
              tabsheetId={list[selectedIndex]?.id}
              containerId={list[selectedIndex]?.containerId}
              previewmode={list[selectedIndex]?.previewmode}
              data={list[selectedIndex]?.component}
              frameIndex={list[selectedIndex]?.frameIndex}
              elementIndex={list[selectedIndex]?.elementIndex}
              sheetIndex={list[selectedIndex]?.sheetId}
              sheetElementIndex={list[selectedIndex]?.sheetElementIndex}
              sheetName={list[selectedIndex]?.Label}
            />
        </div>
        </div>

        {/* SUB MENU */}
        <MenuBox
          containerId={containerId}
          open={showMenu && list[showMenu]?.submenus}
          direction={direction}
          onClose={() => setShowMenu(false)}
          anchorRef={items.get(showMenu)}
          menuItems={list[showMenu]?.submenus}
          onSelect={(item, index) => {
            setactiveIndex(index);
            onSelect(item);
           
          }}
        />
      </div>
    );
};

FlexMenu.defaultProps = {
  offset: 0,
  debounce: 0,
  minWidth: "0px",
  selectedClass: "",
  direction: "ltr",
  template: (props) => <TabBox tab={props} containerId={props.containerId}/>,
  onSelect: () => {},
  actions: [],
};

export default FlexMenu;