   function isDisplayNone(sheet){
    const rules=data?.RuleDetails||[];
  
    let hiddenSheets=[];
    const activityNameRules=rules.activityName||[];
    const dataVariablesRules=rules.dataVariables||[];
    
      activityNameRules.forEach(activityRule=>{
        console.log('asloob',activityRule,functions?.getWorkItemData('activityname'))
        if(activityRule.operator === "=") {
          if(activityRule.value===functions?.getWorkItemData('activityname') && activityRule.hiddenSheets){
            hiddenSheets=[...hiddenSheets,...activityRule.hiddenSheets]
          }
        }
      })
      dataVariablesRules.forEach(dataVarRule=>{
        console.log('asloob',dataVarRule,functions?.getValue(`${dataVarRule.variableName}`))
        if(dataVarRule.operator === "=") {
          if(dataVarRule.value===functions?.getValue(`${dataVarRule.variableName}`) && dataVarRule.hiddenSheets){
            hiddenSheets=[...hiddenSheets,...dataVarRule.hiddenSheets]
          }
        }
        else if(dataVarRule.operator === ">=") {
          if(dataVarRule.value <= functions?.getValue(`${dataVarRule.variableName}`) && dataVarRule.hiddenSheets){
            hiddenSheets=[...hiddenSheets,...dataVarRule.hiddenSheets]
          }
        }
        else if(dataVarRule.operator === "<=") {
          if(dataVarRule.value >= functions?.getValue(`${dataVarRule.variableName}`) && dataVarRule.hiddenSheets){
            hiddenSheets=[...hiddenSheets,...dataVarRule.hiddenSheets]
          }
        }
        else if(dataVarRule.operator === "<") {
          if(dataVarRule.value > functions?.getValue(`${dataVarRule.variableName}`) && dataVarRule.hiddenSheets){
            hiddenSheets=[...hiddenSheets,...dataVarRule.hiddenSheets]
          }
        }
        else if(dataVarRule.operator === ">") {
          if(dataVarRule.value < functions?.getValue(`${dataVarRule.variableName}`) && dataVarRule.hiddenSheets){
            hiddenSheets=[...hiddenSheets,...dataVarRule.hiddenSheets]
          }
        }
      
      })
    
    
    

    return hiddenSheets.includes(sheet);
  }

