const reducer = (state, action) => {
  switch (action.type) {

    case "move":
      // const lastChild = state.children[state.children.length - 1];
      const children = state.children.slice(0, -(action.payload.totalItemRemoved));
      const dropDownChild = state.children.slice(-action.payload.totalItemRemoved);
      return {
        ...state,
        children,
        dropdownItems: [...dropDownChild,...state.dropdownItems],
        inActiveTabWidth: action.payload.lastItem.clientWidth,
        ...(action.payload.lastItem && {
          lastItemWidth: [
            ...state.lastItemWidth,
            action.payload.lastItem.clientWidth,
          ],
        }),
      };

    case "addRemove": 
      const lastItem = state.children[state.children.length-1];
      const itemToRemoveFromChildren = state.children.slice(0, -1);
      const itemToAddInChildren = state.dropdownItems[action.payload.index];
      const finalChildrenItem = [...itemToRemoveFromChildren,itemToAddInChildren];
      const finalDropDownItem = [lastItem,...state.dropdownItems.slice(0,action.payload.index),...state.dropdownItems.slice(action.payload.index+1,state.dropdownItems.length)];

      return {
        ...state,
        children: finalChildrenItem,
        dropdownItems: finalDropDownItem,
        inActiveTabWidth: state.inActiveTabWidth,
        lastItemWidth: state.lastItemWidth,
      };

    case "return":
      const itemsToAdded = state.dropdownItems.slice(0,action.payload.totalItemAdded);
      const dropDown = state.dropdownItems.slice(action.payload.totalItemAdded,state.dropdownItems.length);
      // const [firstItemFromList, ...dropdownItems] = state.dropdownItems;
      // const [_, ...lastItemWidth] = state.lastItemWidth;
      return {
        children: itemsToAdded
          ? [...state.children, ...itemsToAdded]
          : state.children,
        dropdownItems: dropDown,
        inActiveTabWidth: state.inActiveTabWidth,
        lastItemWidth: state.lastItemWidth,
      };
      
    case "reset":
      return {
        children: action.payload,
        dropdownItems: [],
        lastItemWidth: [],
      };
    default:
      return state;
  }
};

export default reducer;
