import makeStyles from "@material-ui/core/styles/makeStyles";

const useStyles = makeStyles((theme) => ({
  PriorityNav_Root: {
    position: "relative",
    display: "flex",
    background: ({ container, actionBar }) =>
      actionBar?.background ? "" : container?.background,
  },
  PriorityNav_Main: {
    display: "flex",
    gap: "2px",
    alignItems: "center",
    overflow: 'hidden',
    padding: "4px",
    paddingBottom:"0px",
    background: ({ container, actionBar }) =>
      actionBar?.background ? container?.background : "",
    minWidth: ({ items, selectedIndex}) => items && items[selectedIndex ? selectedIndex : 0] ? `${items[selectedIndex].clientWidth}px` : '',
  },
  More_menu: {
    "& .labelBox": {
      flexDirection: "row-reverse !important",
    },
  },
  PriorityNav_Item: {
    display:"flex",
    height: "fit-content",
    whiteSpace: 'nowrap',
    background: ({ label }) => label?.background || "inherit",
    border: ({ tabBox }) =>
      tabBox?.border
        ? `${tabBox?.border?.size || "4"}px ${
            tabBox?.border?.type || "solid"
          } ${tabBox?.border?.color || "inherit"}`
        : "",
    color: ({ label }) => label?.color || "inherit",
    borderRadius: ({ tabBox }) => tabBox?.border?.radius || "inherit",
    fontFamily: ({ label }) => label?.fontfamily || "inherit",
    fontStyle: ({ label }) => label?.fontstyle || "normal",
    minWidth: ({ tabBox }) => tabBox?.minWidth || "auto",
    maxWidth: ({ tabBox }) => tabBox?.maxWidth || "100%",
    fontWeight: ({ label }) => label?.weight || "inherit",
    fontSize: ({ label }) => label?.size || "inherit",
    margin:"0px",
    "& button": {
      fontWeight: "inherit",
      fontSize: "inherit",
      opacity: 1,
      minWidth: "inherit",
      maxWidth: "inherit",
      fontFamily: ({ label }) => label?.fontfamily || "inherit",
      fontStyle: ({ label }) => label?.fontstyle || "normal"
    },
    "& .labelBox": {
      fontFamily: ({ label }) => label?.fontfamily || "inherit",
      fontStyle: ({ label }) => label?.fontstyle || "inherit",
      fontWeight: ({ label }) => label?.weight || "inherit",
      fontSize: ({ label }) => label?.size || "inherit",
      display: "flex",
      flexDirection: ({ tabBox }) => {
        const position = tabBox?.icon?.position;
        switch (position) {
          case "start":
            return "row";
          case "end":
            return "row-reverse";
          case "top":
            return "column";
          case "bottom":
            return "column-reverse";
          default:
            break;
        }
      },
      alignItems: ({ tabBox }) =>
        tabBox?.icon?.position === "start" || tabBox?.icon?.position === "end"
        ? "baseline" :
          "center",
    },
  },
  selectedMenu: {
    height: "fit-content",
    fontWeight: ({ label }) => label?.active?.weight || "700",
    border: ({ tabBox }) =>
      tabBox?.activeBorder && tabBox?.activeBorder?.full
        ? `${tabBox?.activeBorder?.size || "4"}px ${
            tabBox?.activeBorder?.type || "solid"
          } ${tabBox?.activeBorder?.color || "#0072C6"}`
        : "",
    borderBottom: ({ tabBox }) =>
      (tabBox?.activeBorder && tabBox?.activeBorder?.full===false && tabBox?.fullBorder)
        ? `${tabBox?.activeBorder?.size || "4"}px ${
            tabBox?.activeBorder?.type || "solid"
          } ${tabBox?.activeBorder?.color || "blue"} !important`
        : "0px",
    color: ({ label }) => label?.active?.color || "inherit",
    fontFamily: ({ label }) => label?.active?.fontfamily || "inherit",
    fontStyle: ({ label }) => label?.active?.fontstyle || "normal",
    fontSize: ({ label }) => label?.active?.size || "inherit",
    background: ({ label }) => label?.active?.background || "inherit",
    "& button": {
      opacity: "1",
      fontWeight: ({ label }) => label?.active?.weight || "700",
      fontFamily: ({ label }) => label?.active?.fontfamily || "inherit",
      fontStyle: ({ label }) => label?.active?.fontstyle || "normal"
    },
    "& button .labelBox ": {
      fontWeight: ({ label }) => label?.active?.weight || "700",
      fontFamily: ({ label }) => label?.active?.fontfamily || "inherit",
      fontStyle: ({ label }) => label?.active?.fontstyle || "normal",
      fontSize: ({ label }) => label?.active?.size || "inherit",
      borderBottom: ({ tabBox }) =>
        tabBox?.fullBorder
          ? ""
          : `${tabBox?.activeBorder?.size || "4"}px ${
              tabBox?.activeBorder?.type || "solid"
            } ${tabBox?.activeBorder?.color || "#0072c6"}`,
    },
    margin: ({ label }) => label?.active?.margin || "0",
    borderRadius: ({ label,tabBox }) => label?.active?.borderRadius || "0" || tabBox?.border?.radius || "inherit",
  },
  contentBox: {
    border: ({ contentBox }) =>
      `${contentBox?.border?.size || "2pt"} ${
        contentBox?.border?.type || "solid"
      } ${contentBox?.border?.color || "#C4C4C4"}`,
    background: ({ contentBox }) => contentBox?.background || "inherit",
    minHeight: ({ contentBox }) => contentBox?.minHeight || "400px",
  },
  actionBar: {
    textAlign: ({ direction }) => (direction === "ltr" ? "right" : "left"),
    justifyContent: "center",
    display: "flex",
    alignItems: "center",
    backgroundColor: ({ actionBar, container }) =>
      actionBar?.background || container?.background || "",
    color: ({ actionBar }) => actionBar?.color || "inherit",
    margin: "0px",
    padding: "5px",
    marginLeft: "auto",
    whiteSpace: "nowrap",
    flexWrap: "nowrap",
  },
  actionItem: {
    marginLeft: "0.5rem",
    padding:"2px 10px",
    fontFamily:"Open Sans"
  },
  actionItemSave: {
    marginLeft: "0.5rem !important",
    padding:"1px 10px !important",
    fontFamily: style => `${style?.actionItemSave?.fontFamily} !important` || "Open Sans !important",
    color: "#ffffff",
    border: "1px solid #0072C6",
    background: "#0072C6",
    marginTop: "0 !important",
    marginBottom: "0 !important",
    fontSize: style => `${style?.actionItemSave?.fontSize} !important` || "12px !important",
  },
  actionItemSaveNext: {
    marginRight: "2px !important",
    marginLeft: "0.5rem !important",
    padding:"2px 10px !important",
    fontFamily: style => `${style?.actionItemSaveNext?.fontFamily} !important` || "Open Sans !important",    
    border: "1px solid #0072C6",
    color: "#0072C6",
    background: "#ffffff",
    marginTop: "0 !important",
    marginBottom: "0 !important",
    fontSize: style => `${style?.actionItemSaveNext?.fontSize} !important` || "12px !important",
  }

}));

export default useStyles;
